$(function () {
});

angular.module("MyApp", ["PageM","FilterM","ToolM", 'cfp.hotkeys'])
         .controller("MyController", ["$scope", "PageProvider", "ToolProvider", "hotkeys", function ($scope, PageProvider,TPM, hotkeys) {
             $scope.PM = PageProvider;
             $scope.TM = {};
             $scope.VM = {};
             $scope.VM.title = "";
             $scope.VM.publishState = "0";

             $scope.changePage = function (pageIndex, noHistory) {
                 $("#tmBlock").show();
                 var pgData = $scope.PM.genPageData(pageIndex);
                 var postData = {};
                 postData['d'] = $scope.VM.title;
                 postData['p'] = $scope.VM.publishState;
                 postData = $scope.PM.filterPageData(pgData, postData);
                 $scope.PM.changePage("/System/DocumentM/DocumentMaintainOP.aspx", postData, function (data) {
                     $scope.TM.data = data.message;
                     $scope.$apply(function () { });
                 });
             };

             hotkeys.add({
                 combo: 'enter',
                 description: 'Description goes here',
                 allowIn: ['INPUT',"BUTTON", 'SELECT', 'TEXTAREA'],
                 callback: function (event, hotkey) {
                     var focusType = document.activeElement.type;
                     if (focusType == "text" || focusType == "select-one" || (focusType == "button" && document.activeElement.name == "searchBtn"))
                     {
                         $scope.changePage(1);
                     }
                 }
             });

             $scope.goDetail = function (record) {
                 $scope.save();

                 var obj = {};
                 obj["i"] = record["I"];
                 TPM.doPOST("/System/DocumentM/DocumentMaintain_Detail.aspx", obj,document.querySelector("#MyForm"));
             };

             $scope.doReset = function () {
                 $scope.VM.publishState = "0";
                 $scope.VM.title = "";
                 $scope.changePage(1);
             };

             $scope.goAdd = function () {
                 $scope.save();

                 var obj = {};
                 TPM.doPOST("/System/DocumentM/DocumentMaintain_Add.aspx", obj, document.querySelector("#MyForm"));
             };

             $scope.save = function () {
                 var data = {};
                 data["title"] = $scope.VM.title;
                 data['publishState'] = $scope.VM.publishState;
                 data['pgNow'] = $scope.PM.pgNow;
                 TPM.setData(data);
                 return JSON.stringify(data);
             };

             if (location.hash.length > 0) {
                 var data = TPM.getData();
                 $scope.VM.title = data.title;
                 $scope.VM.publishState = data.publishState;
                 $scope.changePage(data.pgNow);

             }
             else {
                 $scope.changePage(1);
             }
}]);